import { useState, useEffect, useRef } from 'react'
import * as styles from './Dropbox-style'
import TriangleUp from '../../../assets/icon-triangle-up.svg'
import TriangleDown from '../../../assets/icon-triangle-down.svg'

export default function DropBox({
  type,
  width,
  list,
  setDomain,
  profileData,
  setProfileData,
}) {
  const [isSelected, setIsSelected] = useState()
  const [selectedData, setSelectedData] = useState(
    type === 'email' ? ' ' : ''
  )
  // const previousData = null
  // const [selectedData, setSelectedData] = useState(
  //   previousData ? previousData : ' '
  // )

  //       
  useEffect(() => {
    setTimeout(() => {
      setPreviousCareer()
    }, 500)
  }, [])

  //     (Number -> String)
  function setPreviousCareer() {
    const length = profileData ? profileData.careerLength : 0
    if (type === 'career' && length) {
      switch (length) {
        case 1:
          setSelectedData('1')
          break
        case 2:
          setSelectedData('2')
          break
        case 3:
          setSelectedData('3')
          break
        case 4:
          setSelectedData('4')
          break
        case 5:
          setSelectedData('5')
          break
        case 6:
          setSelectedData('6')
          break
        case 7:
          setSelectedData('7')
          break
        case 8:
          setSelectedData('8')
          break
        case 9:
          setSelectedData('9')
          break
        case 10:
          setSelectedData('10')
          break
        default:
          setSelectedData('')
          break
      }
    }
  }

  //     
  function handleUpdateCareer(length) {
    setProfileData({ ...profileData, careerLength: length })
  }

  //     
  const dropBoxRef = useRef()

  useEffect(() => {
    const handleClickOutside = (e) => {
      if (dropBoxRef.current && !dropBoxRef.current.contains(e.target)) {
        setIsSelected(false)
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
  }, [dropBoxRef])

  return (
    <styles.DropBoxCont ref={dropBoxRef}>
      {!isSelected ? (
        <styles.DropBtn
          width={width}
          onClick={() => {
            setIsSelected((isSelected) => !isSelected)
          }}
        >
          {selectedData}
          <img src={TriangleDown} />
        </styles.DropBtn>
      ) : (
        <>
          <styles.DropBtn
            width={width}
            onClick={() => {
              setIsSelected((isSelected) => !isSelected)
            }}
          >
            {selectedData}
            <img src={TriangleUp} />
          </styles.DropBtn>
          {type === 'email' ? (
            <styles.ListBox width={width}>
              {list.map((item, idx) => {
                return (
                  <styles.List
                    key={idx}
                    onClick={() => {
                      setSelectedData(item)
                      setIsSelected(false)
                      setDomain(item === ' ' ? '' : item)
                    }}
                  >
                    {item}
                  </styles.List>
                )
              })}
            </styles.ListBox>
          ) : (
            <styles.ListBox width={width}>
              {list.map((item, idx) => {
                return (
                  <styles.List
                    key={idx}
                    onClick={() => {
                      setSelectedData(item)
                      setIsSelected(false)
                      handleUpdateCareer(idx)
                    }}
                  >
                    {item}
                  </styles.List>
                )
              })}
            </styles.ListBox>
          )}
        </>
      )}
    </styles.DropBoxCont>
  )
}
