import React, { useContext } from 'react'
import { LocalContext } from '../../../pages/PreviewPage'
import { PreviewProfileItem } from '../../atoms/PreviewItem'
import styled from 'styled-components'
import ColorContext from '../../../context/ColorContext'

export default function ProfilePreview() {
  const { data } = useContext(LocalContext)
  const { mainColor } = useContext(ColorContext)
  const profileData = data.profile
  const commitUrl = `https://ghchart.rshah.org/${
    mainColor.split('#')[1]
  }/${localStorage.getItem('userGithubId')}`

  return (
    <>
      <ProfileSection>
        <ProfileImg>
          <img src={profileData?.profileImg} alt="" />
        </ProfileImg>
        <ProfileBox>
          <span>
            <strong>{profileData?.name}</strong>
            {profileData?.enName}
          </span>
          <DataList>
            {/*  */}
            <PreviewProfileItem
              title=""
              content={profileData?.phoneNumber}
            ></PreviewProfileItem>
            {/*  */}
            <PreviewProfileItem
              title=""
              content={profileData?.fullEmail}
            ></PreviewProfileItem>
            {/*   */}
            <PreviewProfileItem
              title=" "
              type="link"
              content={profileData?.blog}
            ></PreviewProfileItem>
            {/*  */}
            <PreviewProfileItem
              title=" "
              content={
                profileData.careerLength
                  ? `${profileData?.careerLength}`
                  : ''
              }
            ></PreviewProfileItem>
            <img
              src={commitUrl}
              className="commit"
              alt="  "
            />
          </DataList>
        </ProfileBox>
      </ProfileSection>
    </>
  )
}

const ProfileSection = styled.section`
  display: flex;
  gap: 40px;
`

const ProfileImg = styled.div`
  width: 142px;
  height: 142px;
  border-radius: 100px;
  border: 2px solid var(--main-color);
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`

const ProfileBox = styled.div`
  span {
    display: block;
    margin: 0 0 20px 0;
    padding-bottom: 10px;
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    color: var(--main-color);
    border-bottom: 1px solid var(--main-color);
  }

  span strong {
    color: var(--font-color);
    font-size: 24px;
    font-weight: 700;
    padding-right: 12px;
  }
`

const DataList = styled.ul`
  font-size: 14px;

  img.commit {
    width: 100%;
    margin-top: 12px;
  }
`

const Intro = styled.div`
  p {
    word-wrap: break-word;
    line-height: 20px;
  }
`
