import React from 'react'
import styled from 'styled-components'

// NOTE:      type props 
export default function PreviewMonthItem({
  type,
  title,
  date,
  startDate,
  endDate,
  isInvalid,
}) {
  return (
    <Item>
      <DateWrap
        type={type}
        isInvalid={isInvalid}
        startDate={startDate}
        endDate={endDate}
      >
        {isInvalid
          ? '-'
          : type === 'certificate'
          ? date
          : `${startDate} ~ ${endDate}`}
      </DateWrap>
      <p>{title}</p>
    </Item>
  )
}

PreviewMonthItem.defaultProps = {
  date: '-',
  startDate: '-',
  endDate: '-',
  content: '-',
}

const Item = styled.li`
  display: flex;
  align-items: center;
  gap: 16px;

  font-size: 14px;
  margin: 10px auto;
`

const DateWrap = styled.p`
  width: 140px;
  color: #bdbdbd;
  font-weight: 700;
  text-align: ${(props) => props.isInvalid && 'center'};
  text-align: ${(props) => !props.startDate && props.endDate && 'right'};
  padding-right: ${(props) => !props.startDate && props.endDate && '7px'};
`
