import React from 'react'
import styled from 'styled-components'

export default function CompanyInfo() {
  return (
    <Container>
      <div>
        <Term className="ir"></Term>
        <Descript>()</Descript>
      </div>
      <div>
        <Term></Term>
        <Descript></Descript>
      </div>
      <div>
        <Term> </Term>
        <Descript>546-86-01737</Descript>
      </div>
      <div>
        <Term className="ir"></Term>
        <Descript></Descript>
      </div>
      <div>
        <Term></Term>
        <Descript>   330  A 1 106</Descript>
      </div>
    </Container>
  )
}
const Container = styled.dl`
  display: flex;
  flex-wrap: wrap;
  max-width: 80%;
  font-size: 10px;
  color: var(--lightgray-color);
  position: absolute;
  bottom: 40px;

  > div {
    display: flex;
    margin: 2px 0;
  }
  > div + div::before {
    content: '|';
    margin: 0 5px;
  }
`

const Term = styled.dt`
  white-space: nowrap;
  &::after {
    content: ' :';
    margin-right: 5px;
  }
`

const Descript = styled.dd`
  white-space: nowrap;
`
