import { useContext } from 'react'
import { styled } from 'styled-components'
import { MainBtn, SaveBtn } from '../../atoms/Button'
import { ResumeContext } from '../../../context/ResumeContext'
import { useNavigate } from 'react-router-dom'

export default function PreviewBox({ type }) {
  const { resumeData } = useContext(ResumeContext)
  const navigate = useNavigate()

  const saveLocalstorage = () => {
    localStorage.setItem('resumeData', JSON.stringify(resumeData))
    console.log('   - ')
  }

  const movePreview = () => {
    navigate('/preview')
  }

  const moveHome = () => {
    navigate('/')
  }

  return (
    <Cont>
      {type === 'write' ? (
        <>
          <SaveBtn onClick={saveLocalstorage}></SaveBtn>
          <MainBtn type="preview" onClick={movePreview}>
            
          </MainBtn>
        </>
      ) : (
        <>
          <SaveBtn onClick={moveHome}></SaveBtn>
          <MainBtn type="preview">PDF </MainBtn>
        </>
      )}
    </Cont>
  )
}

const Cont = styled.div`
  padding: 14px;
  border-radius: 16px;
  background-color: var(--bg-color);
  display: inline-flex;
  gap: 10px;
  box-shadow: 0px 4px 44px 0px rgba(0, 0, 0, 0.04);
`
