import React, { useState } from 'react'
import { styled } from 'styled-components'
import ComponentHeader from '../ComponentHeader/ComponentHeader'
import { DefaultInput, DefaultTextarea, DateInput } from '../../atoms/Input'
import { WriteSubtitle } from '../../atoms/Title'
import { ProceedingBtn } from '../../atoms/Button'
import { updateData } from '../../../utils'
import Contribution from './Contribution'
import Skills from './Skills'

export default function Project({
  idx,
  project,
  projectData,
  setProjectData,
  handleDelete,
}) {
  const [isStill, setIsStill] = useState(project.inProgress)

  return (
    <ComponentHeader
      id={project.id}
      kind={''}
      title={project.title ? project.title : null}
      handleDelete={handleDelete}
    >
      <Wrap>
        <DefaultInput
          type="text"
          width="738px"
          name="title"
          placeholder=" ."
          onChange={(e) => {
            updateData(e, idx, projectData, setProjectData)
          }}
          inputData={project.title}
        >
          {''}
        </DefaultInput>
        {/*   */}
        <WriteSubtitle subtitle={' '} />
        <Cont>
          <DefaultInput
            name="demoLink"
            type="url"
            onChange={(e) => {
              updateData(e, idx, projectData, setProjectData)
            }}
            inputData={project.demoLink}
          >
            {' '}
          </DefaultInput>
          <DefaultInput
            name="githubLink"
            type="url"
            onChange={(e) => {
              updateData(e, idx, projectData, setProjectData)
            }}
            inputData={project.githubLink}
          >
            {'  '}
          </DefaultInput>
          <DefaultInput
            name="snsLink"
            type="url"
            onChange={(e) => {
              updateData(e, idx, projectData, setProjectData)
            }}
            inputData={project.snsLink}
          >
            {' SNS '}
          </DefaultInput>
        </Cont>
        {/*   */}
        <WriteSubtitle subtitle={' '} name="outline" />
        <DefaultTextarea
          name="outline"
          height="92px"
          placeholder="   ."
          type="project"
          onChange={(e) => {
            updateData(e, idx, projectData, setProjectData)
          }}
          inputData={project.outline}
        >
          {' '}
        </DefaultTextarea>
        {/*     */}
        <WriteSubtitle subtitle={'   '} />
        <Cont>
          <DefaultInput
            type="text"
            name="people"
            onChange={(e) => {
              updateData(e, idx, projectData, setProjectData)
            }}
            inputData={project.people}
            placeholder=") Front-End 4, Back-End 2"
          >
            {''}
          </DefaultInput>
          <DateWrap>
            <DateInput
              name="startDate"
              width="220px"
              inputData={project.startDate}
              onChange={(e) => {
                updateData(e, idx, projectData, setProjectData)
              }}
            >
              {''}
            </DateInput>
            <Tilde>~</Tilde>
            <DateInput
              idx={idx}
              name="endDate"
              width="220px"
              inputData={project.endDate}
              onChange={(e) => {
                updateData(e, idx, projectData, setProjectData)
              }}
              isStill={isStill}
            >
              {''}
            </DateInput>
            <ProceedingBtn
              name="inProgress"
              value={isStill}
              idx={project.id}
              type="project"
              inputData={project.inProgress}
              onChange={(e) => {
                setProjectData(
                  projectData.map((el, i) =>
                    i === idx ? { ...el, inProgress: !isStill } : el
                  )
                )
                setIsStill(!isStill)
              }}
              isStill={isStill}
            />
          </DateWrap>
        </Cont>
        {/*   */}
        <WriteSubtitle subtitle={' '} />
        <Contribution
          idx={idx}
          id={project.id}
          contributions={project.contributions}
          projectData={projectData}
          setProjectData={setProjectData}
        />
        {/*   */}
        <WriteSubtitle subtitle={' '} />
        <Skills
          idx={idx}
          id={project.id}
          skills={project.skills}
          projectData={projectData}
          setProjectData={setProjectData}
        />
      </Wrap>
    </ComponentHeader>
  )
}

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
`

const Cont = styled.div`
  display: flex;
  flex-direction: column;
  gap: 24px;
`

const DateWrap = styled.div`
  display: flex;
  align-items: center;
`

const Tilde = styled.p`
  font-size: 14px;
  font-weight: 500;
  margin: 20px 8px 0 8px;
`
